<template lang="pug">
.container(class="py-5")
  .form-row(class="mt-gutter-s")
    .col-4
      router-link(to="/spider")
        #spiderBox(class="box bg-spider rounded h-100")
          spider(class="spider")

    .col-8
      router-link(to="/candle")
        #candleBox(class="box bg-candle rounded")
          candle(class="candle")
      router-link(to="/pumpkin")
        #pumpkinBox(class="longbox bg-pumpkin mt-gutter-s rounded")
          pumpkin(class="pumpkin")

  .form-row(class="mt-gutter-s")
    .col-12
      router-link(to="/gost")
        #gostBox(class="box bg-gost rounded")
          gost(class="gost")

  .form-row(class="mt-gutter-s")
    .col-6
      router-link(to="/pot")
        #potBox(class="longbox bg-pot rounded")
          pot(class="pot")
    .col-6
      router-link(to="/hand")
        #handBox(class="longbox bg-hand rounded")
          hand(class="hand")
</template>

<script>
import gost from './gost'
import pot from './pot'
import hand from './hand'
import candle from './candle'
import pumpkin from './pumpkin'
import spider from './spider'

export default {
  name: 'Dashboard',
  components: {
    gost,
    pot,
    hand,
    candle,
    pumpkin,
    spider
  },
  data () {
    return {
    }
  },
  created () {
  }
}
</script>

<style scoped>
.gost {
  width: 35%;
}

.pumpkin {
  width: 100%;
}

.spider {
  transform: translateY(-30px) scale(1.3);
}

.candle {
  width: 45%;
  max-width: 280px;
}

.pot {
  width: 65%;
  max-width: 320px;
}

.hand {
  width: 58%;
  max-width: 320px;
}

.bg-gost {
  background-color: #7c5db0;
}

.bg-hand {
  background-color: #188c74;
}

.bg-pot {
  background-color: #d69fe9;
}

.bg-pumpkin {
  background-color: #40bcff;
}

.bg-spider {
  background-color: #00d1d3;
}

.bg-candle {
  background-color: #ee4a65;
}
</style>



